import React, { Component } from 'react';
import ReactMapboxGl, { Layer, Feature } from 'react-mapbox-gl';

const Map = ReactMapboxGl({
  accessToken: process.env.REACT_APP_MAPBOX_TOKEN,
  dragPan: false
});

class FormMap extends Component {

  state = {
    dragCounter: 0,
    zoomCounter: 0,
    pitchCounter: 0,
    touchMoveCounter: 0,
  };

  onDrag = () => this.setState(p => ({dragCounter: p.dragCounter + 1}));
  onZoom = () => this.setState(p => ({zoomCounter: p.zoomCounter + 1}));
  onPitch = () => this.setState(p => ({pitchCounter: p.pitchCounter + 1}));
  onTouchMove = () => this.setState(p => ({touchMoveCounter: p.touchMoveCounter + 1}));

  onDragEnd = e => {
    const { passMapCoordinates } = this.props;
    const lng = e.lngLat.lng;
    const lat = e.lngLat.lat;
    passMapCoordinates({ lng, lat });
  };

  render() {
    const { centerLng, centerLat } = this.props;
    return (
      <div>
        <Map style="mapbox://styles/mapbox/streets-v9"
             center={{ lng: centerLng, lat: centerLat }}
             className="formMap"
             onDrag={this.onDrag}
             onZoom={this.onZoom}
             onPitch={this.onPitch}
             onTouchMove={this.onTouchMove}
        >
          <Layer type="circle"
                 id="marker"
                 paint={{
                   'circle-color': '#ff5200',
                   'circle-stroke-width': 1,
                   'circle-stroke-opacity': 1,
                   'circle-radius': 10
                 }}>
            <Feature coordinates={[centerLng, centerLat]} draggable
                     onDragEnd={e => this.onDragEnd(e)}/>
          </Layer>
        </Map>
        dragCounter: {this.state.dragCounter} <br/>
        zoomCounter: {this.state.zoomCounter} <br/>
        pitchCounter: {this.state.pitchCounter} <br/>
        touchMoveCounter: {this.state.touchMoveCounter} <br/>
      </div>
    );
  }
}

export default FormMap;
